<script lang="ts">
  import { onMount } from "svelte";
  import CodeEditor from "./components/CodeEditor/CodeEditor.svelte";

  // TODO: move to rollup config somehow?
  import loadDiagwiz from "../../src/wasm/Cargo.toml";

  const debounce = (f, timeout = 250) => {
    let timer: str;
    return (...v) => {
      clearTimeout(timer);
      timer = setTimeout(() => f(...v), timeout);
    };
  };

  const measureTime = (f) => {
    let t0 = performance.now();
    f();
    let t1 = performance.now();
    return (t1 - t0).toFixed(3);
  };

  let loaded = false;
  let stdout = "";
  let stderr = "";
  let errorCount = 0;
  let executionTime = 0;

  const initialContent = `
alias ali = "Alice"

ali->Bob: "Hello!"
Bob->Bob: "(Bob thinks)"
Bob->ali: "Hello back!"
`.trim();

  let diagwiz = null;

  loadDiagwiz().then((exports) => {
    diagwiz = exports;
    loaded = true;
  });

  function preview(input) {
    let output;
    if (!diagwiz) {
      stderr = "Could not load diagwiz. Does your browser support WebAssembly?";
      return;
    }
    executionTime = measureTime(() => {
      try {
        output = diagwiz.render(`${input}\n`);
        stdout = output;
        stderr = "";
      } catch (e) {
        stderr = e;
      }
    });
  }

  $: errorCount = Math.min(1, stderr.length);
</script>

<div class="container">
  {#if loaded}
    <CodeEditor {initialContent} onUpdate={debounce(preview)} />
    <div class="output">
      <div class="output-panel">
        <div class="output-header output-header-composite">
          <h3>Output</h3>
          <div>Execution time: {executionTime} ms</div>
        </div>
        <pre class="output-content">{stdout}</pre>
      </div>
      <div class="output-panel">
        <h3 class="output-header" class:output-header-alert={errorCount}>
          Errors ({errorCount})
        </h3>
        <pre class="output-content output-content-padded">{stderr}</pre>
      </div>
    </div>
  {/if}
</div>

